import React, {Component} from "react";
import ConvergentAnalyzerAdapter from "../vasModal/ConvergentAnalyzerAdapter";
import {createCustomRules} from "../../../../analyzer/DeviceUtils"
import ConvergentSecondaryChoiceBonusModal from "../../fix/buymore/ConvergentSecondaryChoiceBonusModal";

export class ConvergentFixBonusModal extends Component {

    render() {
        const configurables = JSON.parse(JSON.stringify(this.props.convergentConfigurables.configurables));
        let secondaryConfigurable = configurables.find(conf => this.props.isSecondaryConfigurable(conf));

        let enrichedBonus = {...this.props.bonus, propositionId : secondaryConfigurable.propositionId};
        return(
            <div>
                <ConvergentAnalyzerAdapter
                    configurables={configurables}
                    entry={this.props.entry}
                    updateCartProducts={this.props.updateCartProducts}
                    customRules={this.props.customRules || createCustomRules(this.props.services, this.props.migrated)}
                    ref={(adapter) => {this.adapter = adapter}}
                >
                    <ConvergentSecondaryChoiceBonusModal
                        uid={this.props.uid}
                        label={this.props.label}
                        open={this.props.fixBonusModalVisible}
                        services={this.props.services}
                        bonus={enrichedBonus}
                        onClose={this.props.onClose}
                        descriptions={this.props.descriptions}
                        showNetPrices={this.props.showNetPrices}
                        factory={secondaryConfigurable.factoryName}
                        configurables={configurables}
                        migratedProducts={this.props.migratedProducts}
                    />
                </ConvergentAnalyzerAdapter>
            </div>
        );
    }

}